.content{
    width: 100%;
    height: 75vh;
    display: flex;
}
.img{
    width: 70%;
}
.img img{
    width: 100%;
    height: 100%;
}

.info{
    width: 30%;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.welcome{
    height: 50%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

}

.welcome h1{
    color: royalblue;
}

.infoButtons{
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.infoButtons button{
    width: 90px;
    height: 50px;
    margin: 10px;
    background-color: royalblue;
    border: 2pt solid white;
    border-radius: 8px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    font-size: medium;
}

.infoButtons button:hover{
    background-color: transparent;
    color: royalblue;
    border-color: royalblue;
}

.infoButtons a button{
    outline: none;
}

@media screen and (max-width: 600px) {
    .img{
        display: none;
    }
    .info{
        width: 100%;
    }
}